<template>
    <div class="list">
        <cell class="item" v-for="item in accountList" 
            title="账号" label="账户类型" :value="item.account" :desc="item.accountType" :key=" item.id " 
            :to="{ path: 'info', query: { id: item.id } }"  is-link>
        </cell>
    </div>
</template>

<script>


import * as service from '@/services/relationship-change';

export default {
    data() {
        return {
            accountList: []
        };
    },
    created() {
        this.accountList = Object.assign({}, this.$store.state.relationshipChange.search).accountList;
        if(this.accountList == null || this.accountList.length == 0){
            service.getRelationshipChangeIndex().then((res) => {
                this.accountList = res.data.data;
            }, (err) => {
                console.log( err );
            });
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateRelationshipChangeSearch', {accountList: this.accountList} );
        }else{
            this.$store.dispatch('clearRelationshipChangeSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.list
    {
        .item
        {
            .cell-label,.cell-desc
            {
                font-size: 13px!important;
                color: #333!important;
            }
        }
    }
</style>



