<template>
    <div class="device-revocation-row">
        <div class="div-success-center"><b>操作成功!</b></div>
        <div class="device-revocation-detail">            
            <mt-cell label="账户：" :value="detail.account"></mt-cell>
            <mt-cell label="账户类型：" :value="detail.accountType"></mt-cell>
            <mt-cell label="日累计限额：" :value="detail.dateMoney"></mt-cell>
            <mt-cell label="年累计限额：" :value="detail.yearMoney"></mt-cell>
            <mt-cell label="账户余额上限：" :value="detail.upMoney"></mt-cell>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            detail: {}
        };
    },
    created() {
        this.detail = this.$ls.getItem('accountAccountLiftLevelSuccess');
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-3);
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .div-success-center {
        height: 90px;
        line-height: 90px;
        text-align: center;
        font-size: 30px;
        background: #fff;
        border: 1px solid #ddd;
    }
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
</style>



