<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-field label="客户姓名：" placeholder="请输入客户姓名" v-model="payDetail.userName" readonly></mt-field>
            <mt-field label="账户类型：" placeholder="请输入账户类型" v-model="payDetail.accountType" readonly></mt-field>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">设置交易密码:</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-field label="交易密码：" placeholder="请输入交易密码" v-model="payPass.pwd1"></mt-field>
            <mt-field label="再次输入：" placeholder="请输入输入确认密码" v-model="payPass.pwd2"></mt-field>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
            <mt-button @click="next()" size="large" type="primary">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/account-opening';
import { Toast } from 'mint-ui-rem-dpr';

export default {
    data() {
        return {
            payDetail: {
                userName: '',
                accountType: ''
            },
            payPass: {
                pwd1: '',
                pwd2: ''
            }
        };
    },
    created() {
      this.payDetail.userName = this.$route.query.userName || '';
      this.payDetail.accountType = this.$route.query.accountType || '';
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.payPass);
            if (this.$validate.isEmpty(_data.pwd1)) {
                this.doMsg('请输入交易密码');
                return false;
            }
            if (this.$validate.isEmpty(_data.pwd2)) {
                this.doMsg('请输入输入确认密码');
                return false;
            }
            if (_data.pwd1.length != 6) {
                this.doMsg('交易密码长度为6位数字');
                return false;
            }
            if (_data.pwd1 != _data.pwd2) {
                this.doMsg('两次密码不一致');
                return false;
            }
            service.getAccountOpeningSuccess().then((res) => {
                this.$ls.setItem("accountOpeningSuccess", res.data);
                var params = {
                    path: '/account-opening/success',
                    query: {
                        account: 2,
                        accountType: 1
                    }
                };
                this.$router.push(params);
            }, (err) => {
              console.log( err );
            });
        },
        doMsg(msg) {
            Toast({
                message: msg,
                iconClass: 'error',
                duration: 1000
            });
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



