<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="客户姓名：" placeholder="请输入客户姓名" :value="accountDetail.userName" readonly></mt-cell>
            <mt-cell label="身份证号码：" placeholder="请输入身份证号码" :value="accountDetail.idNo" readonly></mt-cell>
            <mt-cell label="手机号：" placeholder="请输入手机号" :value="accountDetail.phone" readonly></mt-cell>
            <mt-cell class="mint-select" title="申请账户类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.accountType">
                    <option v-for="item in accountTypeData" key="item.id" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">绑定账户信息:</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-cell class="mint-select" title="账户来源：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.action">
                    <option v-for="item in actionData" key="item.id" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="账号：" placeholder="请输入申请账户" v-model="accountInfo.accountNo"></mt-field>
            <mt-cell class="mint-select" title="账户类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.type">
                    <option v-for="item in typeData" key="item.id" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <div style="height:50px;line-height:50px">
            <input type="checkbox" v-model="accountInfo.checkbox" style="width: 20px;height: 20px;margin-top: -2px;margin-bottom: 1px;vertical-align: middle;">
            <span style="color:red"><a href="#doc">我同意《个人开立电子结算账户申请协议书》</a></span>
        </div>
        <div class="device-revocation-detail">
            <div class="detail-items">
                <span style="display:block"><b>温馨提示：</b></span>
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Ⅱ类账户转账限额为日累计1万元，年累计20万元。Ⅲ类账户转账限额为日累计5000元，年累计10万元，账户余额不可大于1000元。绑定账户需为本人本行或他行的Ⅰ类账户或信用卡账户。</span>
            </div>
        </div>
        <div class="field-action">
            <mt-button @click="back()" type="primary">返回</mt-button>
            <submit-button @submit="next()" :submitStatus="submitStatus" type="submit">下一步</submit-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/account-opening';
import { mapGetters, mapActions, mapState } from 'vuex';
import { Toast } from 'mint-ui-rem-dpr';

export default {
    data() {
        return {
            accountTypeData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '1',
                    label: 'Ⅱ类账户'
                },
                {
                    value: '2',
                    label: 'Ⅲ类账户'
                }
            ],
            actionData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '1',
                    label: '本行'
                },
                {
                    value: '2',
                    label: '他行'
                }
            ],
            typeData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '2',
                    label: '借记卡'
                },
                {
                    value: '3',
                    label: '中国农业银行'
                }
            ],
            submitStatus: false,
            accountDetail: {
                userName: '',
                idNo: '',
                phone: ''
            },
            accountInfo: {
                accountType: '',
                action: '',
                accountNo: '',
                type: '',
                checkbox: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign({}, this.$store.state.accountOpening.search);

        var id = this.$route.query.id || '';
        service.getAccountOpeningIndex().then((res) => {
            this.accountDetail = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    computed: {
    },
    components: {
    },
    watch: {
        accountInfo: {
            handler(item){
                if(item.accountNo && item.accountType && item.action && item.type){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            console.log(this.accountInfo.action);
            let _data = Object.assign({}, this.accountInfo);
            if (this.$validate.isEmpty(_data.accountType)) {
                this.doMsg('请选择申请账户类型');
                return false;
            }
            if (this.$validate.isEmpty(_data.action)) {
                this.doMsg('请选择账户来源');
                return false;
            }
            if (this.$validate.isEmpty(_data.accountNo)) {
                this.doMsg('请输入申请账户');
                return false;
            }
            if (this.$validate.isEmpty(_data.type)) {
                this.doMsg('请选择账户类型');
                return false;
            }
            if (!_data.checkbox) {
                this.doMsg('请先同意协议书');
                return false;
            }

            var params = {
                path: '/account-opening/pay',
                query: {
                    userName: this.accountDetail.userName,
                    accountType: this.accountInfo.accountType
                }
            };
            this.$router.push(params);
        },
        doMsg(msg) {
            Toast({
                message: msg,
                iconClass: 'error',
                duration: 1000
            });
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateAccountOpeningSearch', this.accountInfo );
        }else{
            this.$store.dispatch('clearAccountOpeningSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



