<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="账号：" :value="accountInfo.account"></mt-cell>
            <mt-cell label="账户类型：" :value="accountInfo.accountType | accountTypeFilter"></mt-cell>
            <mt-cell class="mint-select" title="操作类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.operationType">
                    <option v-for="item in operationTypeData" key="item.id" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
            <div class="detail-items">
                <cell-password :options=" cellPassword " ></cell-password>
            </div>
        </div>
        <div class="device-revocation-detail detail-items-li">
            <div class="detail-items">
                <span style="display:block"><b>温馨提示：</b></span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Ⅱ、Ⅲ类账户可升级为Ⅰ、Ⅱ类账户；Ⅰ、Ⅱ类账户可降级为Ⅱ、Ⅲ类账户，不可跨级升降。</span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Ⅱ类升Ⅰ类必须为办理了实体卡的Ⅱ类账户；Ⅱ类降Ⅲ类必须为非实体卡的Ⅱ类账户。</span>
            </div>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
            <mt-button @click="next()" size="large" type="primary">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/account-lift-level';
import { Toast, MessageBox } from 'mint-ui-rem-dpr';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            operationTypeData: [],
            accountInfo: {
                account: '',
                accountType: '',
                operationType: '',
                userName: '',
                userIdNo: '',
                phone: '',
                action: '',
                type: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign(this.accountInfo, this.$store.state.accountLiftLevel.search.accountInfo);
        if(!this.accountInfo.account){
            var id = this.$route.query.id || '';
            var operationTypeData1 = {value: '1|升级', label: '升级'};
            var operationTypeData2 = {value: '2|降级', label: '降级'};
            service.getAccountLiftLevelInfo1().then((res) => {
                if(res.data.accountType == '1') {
                    this.operationTypeData.push(operationTypeData2);
                    this.accountInfo.operationType = '2|降级';
                }
                if(res.data.accountType == '2') {
                    this.operationTypeData.push(operationTypeData1);
                    this.operationTypeData.push(operationTypeData2);
                    this.accountInfo.operationType = '1|升级';
                }
                if(res.data.accountType == '3') {
                    this.operationTypeData.push(operationTypeData1);
                    this.accountInfo.operationType = '1|升级';
                }
                this.accountInfo.account = res.data.account;
                this.accountInfo.accountType = res.data.accountType;
            }, (err) => {
                console.log( err );
            });
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.accountInfo);
            var operationType = '';
            var operationTypeName = '';
            var accountType = this.accountInfo.accountType;
            var accountTypeName = '';
            var newAccountTypeName = '';
            if(this.accountInfo.operationType != null) {
                operationType = this.accountInfo.operationType.split('|')[0];
                operationTypeName = this.accountInfo.operationType.split('|')[1];
            }
            if (this.$validate.isEmpty(_data.operationType)) {
                this.doMsg('请选择操作类型');
                return false;
            }
            // if (this.$validate.isEmpty(_data.pwd)) {
            //     this.doMsg('请输入输入交易密码');
            //     return false;
            // }
            // if (_data.pwd.length != 6) {
            //     this.doMsg('交易密码长度为6位数字');
            //     return false;
            // }
            if (operationTypeName == '升级'){
                if (accountType == '2') {
                    accountTypeName = 'Ⅱ类';
                    newAccountTypeName = 'Ⅰ类';
                }
                if (accountType == '3') {
                    accountTypeName = 'Ⅲ类';
                    newAccountTypeName = 'Ⅱ类';
                }
            }else{
                if (accountType == '1') {
                    accountTypeName = 'Ⅰ类';
                    newAccountTypeName = 'Ⅱ类';
                }
                if (accountType == '2') {
                    accountTypeName = 'Ⅱ类';
                    newAccountTypeName = 'Ⅲ类';
                }
            }
            MessageBox({
                title: '是否' + operationTypeName + '账户',
                message: '是否将' + accountTypeName + '账户' + operationTypeName + '为' + newAccountTypeName + '账户？',
                showCancelButton: true
            }).then(action => {
                if(action === 'confirm'){
                    service.getAccountLiftLevelInfo2().then((res) => {
                        this.accountInfo.userName = res.data.userName;
                        this.accountInfo.userIdNo = res.data.userIdNo;
                        this.accountInfo.phone = res.data.phone;
                        var params = {
                            path: '/account-lift-level/info2',
                            query: {
                                account: this.accountInfo.account,
                                accountTypeName: newAccountTypeName
                            }
                        };
                        this.$router.push(params);
                    }, (err) => {
                      console.log( err );
                    });
                }
            });
        },
        doMsg(msg) {
            Toast({
                message: msg,
                iconClass: 'error',
                duration: 1000
            });
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            var accountList = Object.assign({}, this.$store.state.accountLiftLevel.search).accountList || [];
            this.$store.dispatch('updateAccountLiftLevelSearch', {accountList: accountList, accountInfo: this.accountInfo} );
        }else{
            this.$store.dispatch('clearAccountLiftLevelSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



