import ajax from './ajax';
import api from './api';

export function getAccountCancellationIndex(data) {
    return ajax.post( api.getAccountCancellationIndex, data );
}

export function getAccountCancellationInfo(data) {
    return ajax.post( api.getAccountCancellationInfo, data );
}

export function getAccountCancellationSuccess(data) {
    return ajax.post( api.getAccountCancellationSuccess, data );
}



