import ajax from './ajax';
import api from './api';

export function getAccountLiftLevelIndex(data) {
    return ajax.post( api.getAccountLiftLevelIndex, data );
}

export function getAccountLiftLevelInfo1(data) {
    return ajax.post( api.getAccountLiftLevelInfo1, data );
}

export function getAccountLiftLevelInfo2(data) {
    return ajax.post( api.getAccountLiftLevelInfo2, data );
}

export function getAccountLiftLevelSuccess(data) {
    return ajax.post( api.getAccountLiftLevelSuccess, data );
}



