<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="客户姓名：" :value="accountInfo.userName"></mt-cell>
            <mt-cell label="身份证号：" :value="accountInfo.userIdNo"></mt-cell>
            <mt-cell label="手机号：" :value="accountInfo.phone"></mt-cell>
            <mt-cell label="Ⅱ/Ⅲ类户:" :value="accountInfo.account"></mt-cell>
            <div class="detail-items">
                <cell-password :options=" cellPassword " ></cell-password>
            </div>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">绑定账户信息：</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-cell class="mint-select" title="账户来源：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.action">
                    <option v-for="item in actionData" key="item.id" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="账号：" placeholder="请输入账号" v-model="accountInfo.newAccount"></mt-field>
            <mt-cell class="mint-select" title="账户类型：">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.type">
                    <option v-for="item in typeData" key="item.id" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
            <mt-button @click="next()" size="large" type="primary">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/relationship-change';
import { Toast } from 'mint-ui-rem-dpr';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            actionData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '1',
                    label: '本行'
                },
                {
                    value: '2',
                    label: '他行'
                }
            ],
            typeData: [
                {
                    value: '',
                    label: '请选择'
                },
                {
                    value: '2',
                    label: '借记卡'
                },
                {
                    value: '3',
                    label: '中国农业银行'
                }
            ],
            accountInfo: {
                account: '',
                accountType: '',
                operationType: '',
                userName: '',
                userIdNo: '',
                phone: '',
                action: '',
                newAccount: '',
                type: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign(this.accountInfo, this.$store.state.relationshipChange.search.accountInfo);
        if(!this.accountInfo.account){
            var id = this.$route.query.id || '';
            service.getRelationshipChangeInfo().then((res) => {
                this.accountInfo.account = res.data.account;
                this.accountInfo.accountType = res.data.accountType;
                this.accountInfo.userName = res.data.userName;
                this.accountInfo.userIdNo = res.data.userIdNo;
                this.accountInfo.phone = res.data.phone;
            }, (err) => {
                console.log( err );
            });
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.accountInfo);
            if (this.$validate.isEmpty(_data.action)) {
                this.doMsg('请选择账号来源');
                return false;
            }
            if (this.$validate.isEmpty(_data.newAccount)) {
                this.doMsg('请输入账号');
                return false;
            }
            if (this.$validate.isEmpty(_data.type)) {
                this.doMsg('请选择账号类型');
                return false;
            }
            service.getRelationshipChangeSuccess().then((res) => {
                this.$ls.setItem("accountRelationshipChangeSuccess", res.data);
                var params = {
                    path: '/relationship-change/success',
                    query: {
                        account: _data.account,
                        accountType: _data.accountType
                    }
                };
                this.$router.push(params);
            }, (err) => {
              console.log( err );
            });
        },
        doMsg(msg) {
            Toast({
                message: msg,
                iconClass: 'error',
                duration: 1000
            });
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



