<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">            
            <mt-cell label="账户：" :value="detail.account"></mt-cell>
            <mt-cell label="账户类型：" :value="detail.accountType | accountTypeFilter"></mt-cell>
            <mt-cell label="日转出额度：" :value="detail.dataIntoQuoto"></mt-cell>
            <mt-cell label="日转入额度：" :value="detail.dataOutQuoto"></mt-cell>
            <mt-cell label="年转出额度：" :value="detail.yearIntoQuoto"></mt-cell>
            <mt-cell label="年转入额度：" :value="detail.yearOutQuoto"></mt-cell>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/quota-inquiry';

export default {
    data() {
        return {
            detail: []
        };
    },
    created() {
        service.getQuotaInquiryIndex().then((res) => {
            this.detail = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .div-success-center {
        height: 90px;
        line-height: 90px;
        text-align: center;
        font-size: 30px;
        background: #fff;
        border: 1px solid #ddd;
    }
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
</style>



