<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="账号：" :value="accountInfo.account"></mt-cell>
            <mt-cell label="账户类型：" :value="accountInfo.accountType | accountTypeFilter"></mt-cell>
            <mt-cell label="绑定账户：" :value="accountInfo.bindAccount"></mt-cell>
            <mt-cell label="账户余额:" :value="accountInfo.balance"></mt-cell>
            <div class="detail-items">
                <cell-password :options=" cellPassword " ></cell-password>
            </div>
        </div>
        <div class="device-revocation-detail detail-items-li">
            <div class="detail-items">
                <span style="display:block"><b>温馨提示：</b></span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                1、若绑定账户为他行账户，请携本人身份证前往安徽农金网点办理销户。</span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2、若绑定账户为本行账户，Ⅱ、Ⅲ类账户销户后，账户余额及结息将自动转回绑定账户中。</span>
                <span style="display:block">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                3、若绑定账户已销户，则无法操作此交易，请先进行绑定账户变更。</span>
            </div>
        </div>
        <div class="field-action">
            <mt-button @click="back()" size="large" type="primary">返回</mt-button>
            <mt-button @click="next()" size="large" type="primary">下一步</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/account-cancellation';
import { Toast } from 'mint-ui-rem-dpr';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            accountInfo: {
                account: '',
                accountType: '',
                bindAccount: '',
                balance: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign(this.accountInfo, this.$store.state.accountCancellation.search.accountInfo);
        if(!this.accountInfo.account){
            var id = this.$route.query.id || '';
            service.getAccountCancellationInfo().then((res) => {
                this.accountInfo.account = res.data.account;
                this.accountInfo.accountType = res.data.accountType;
                this.accountInfo.bindAccount = res.data.bindAccount;
                this.accountInfo.balance = res.data.balance;
                console.log(this.accountInfo);
            }, (err) => {
                console.log( err );
            });
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.accountInfo);
            // if (this.$validate.isEmpty(_data.type)) {
            //     this.doMsg('请输入密码');
            //     return false;
            // }
            service.getAccountCancellationSuccess().then((res) => {
                this.$ls.setItem("accountCancellationSuccess", res.data);
                var params = {
                    path: '/account-cancellation/success',
                    query: {
                        account: _data.account,
                        accountType: _data.accountType
                    }
                };
                this.$router.push(params);
            }, (err) => {
              console.log( err );
            });
        },
        doMsg(msg) {
            Toast({
                message: msg,
                iconClass: 'error',
                duration: 1000
            });
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



