import ajax from './ajax';
import api from './api';

export function getAccountOpeningIndex(data) {
    return ajax.post( api.getAccountOpeningIndex, data );
}

export function getAccountOpeningPay(data) {
    return ajax.post( api.getAccountOpeningPay, data );
}

export function getAccountOpeningSuccess(data) {
    return ajax.post( api.getAccountOpeningSuccess, data );
}



