import ajax from './ajax';
import api from './api';

export function getRelationshipChangeIndex(data) {
    return ajax.post( api.getRelationshipChangeIndex, data );
}

export function getRelationshipChangeInfo(data) {
    return ajax.post( api.getRelationshipChangeInfo, data );
}

export function getRelationshipChangeSuccess(data) {
    return ajax.post( api.getRelationshipChangeSuccess, data );
}



